.user {
    position: relative;
    top: -70px;
    margin-bottom: 40px;
}

.avatar {
    margin-right: 60px;
}


.avatar img {
    width: 120px;
    border-radius: 50%;
    border: 2px solid white;
    box-shadow: 0px 0px 5px black;
}
@media (max-width: 769px) {
    .avatar img {
        width: 210px;
    }
}

